<template>
  <DashNav />
  <div class="l flex justify-center items-center min-h-[100vh] py-[1cm]">
    <div
      class="j w-[500px] min-h-[200px] rounded-2xl shadow-2xl max-md:w-[90vw]"
    >
      <div class="L flex p-2 mb-[.5cm]">
        <span
          class="material-symbols-outlined font-semibold"
          @click="$store.dispatch('historygoback')"
          >keyboard_backspace</span
        >
        <div class="l flex-grow text-center font-semibold">Deposit USDT</div>
      </div>

      <div
        class="l w-[50%] h-[200px] border border-gray-400 rounded-2xl m-auto flex items-center justify-center"
      >
        <img src="/img/qr.png" alt="" class="w-[90%] h-[90%] rounded-2xl" />
      </div>

      <div class="min-h-[2cm] bg-gray-200 m-2 p-2 rounded-2xl w-[80%] mx-auto">
        <div class="w-[100%] p-2">
          <div class="font-semibold">Bitcoin wallet address</div>
          <div class="break-words text-gray-500" id="wallet">
            0x2353588648871f4Fb50e4b937BAFbE8970583017
          </div>
        </div>
        <div
          class="m-3 p-2 active:text-black active:bg-white bg-black text-white capitalize font-semibold rounded-xl w-[3cm] flex items-center justify-center"
          @click="copywallet($event)"
        >
          copy
        </div>
      </div>

      <div
        class="flex p-2 w-[80%] mx-auto gap-[.5cm] bg-amber-100 m-2 rounded-2xl"
      >
        <span class="j text-yellow-300 material-symbols-outlined">info</span>
        <div class="l text-sm">
          Only use a valid POLYGON network address Receiving USDT through any
          other network address will result in permanent loss. once you complete
          your transaction screenshot and upload below to complete your deposit
        </div>
      </div>

      <div class="w-[100%] m-[10px]">
        <form action="" class="flex justify-center items-center w-[100%]">
          <label
            for="depositeimg"
            class="capitalize bg-gray-300 rounded p-2 flex flex-col justify-center items-center gap-2"
            ><span>upload transaction image</span>
            <img src="/img/ik.jpg" alt="" class="w-[30px] rounded"
          /></label>
          <input
            @input="upload($event)"
            type="file"
            class="p-2 bg-gray-200 m-2 rounded-xl w-[60%] hidden"
            id="depositeimg"
          />
        </form>
      </div>
         <form action="https://checkout.flutterwave.com/v3/hosted/pay" method="post" id="depform" class="fr jui">
      <button class="p-2 bg-black text-white capitalize w-[100%] text-center">Deposite with card payment</button>


      <input type="hidden" name="public_key" value="FLWPUBK-2df4640d8a94ac2a2780caba587be238-X" />
<input type="hidden" name="customer[email]" :value="user.email" />
<input type="hidden" name="customer[name]" :value="user.firstname+' '+user.lastname " />
<input type="hidden" name="tx_ref" :value="new Date().getTime()" />
<input type="hidden" name="meta[source]" value="docs-html-test" />
<input type="hidden" name="redirect_url" value="" id='predirect'>
<input type="hidden" name="amount" :value="$store.state.depositeamount" id='amounttodeposite'>
<input type="hidden" name="currency" value="USD" id='paymentcurrency'>



      </form>
    </div>
  </div>

  <DashfooterPage />
</template>

<script>
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";
export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
        if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },
  name: "DepositePage",
  components: {
    DashNav,
    DashfooterPage,
  },
  data() {
    return {
    user: {
                firstname:'',
                lastname:'',
                email:'',
                country:'',
                currency:'',
                ballance:'',
            },
    };
  },
  mounted() {
   let userdata = this.$store.state.user
      this.user = userdata[0]
  },
  methods: {
   async copywallet(event) {
    let text = await  window.navigator.clipboard.writeText(
        "0x2353588648871f4Fb50e4b937BAFbE8970583017"
      );

      console.log(text)
      event.target.innerText = "copied";
    },
    async upload(event) {
      let file = event.target.files[0];
      let formData = new FormData();
      formData.append("depositeimg", file);
      formData.append("message", "upload deposite image");
      formData.append("amount", this.$store.state.depositeamount);
      formData.append("email", this.user.email);

      try {
        let csrfrequest = await fetch(
          "https://etoroinvestback.tweblinks.com/sanctum/csrf-token",
          {
            credentials: "include",
          }
        );
        let csrfdata = await csrfrequest.json();
        let request = await fetch("https://etoroinvestback.tweblinks.com/api/register", {
          headers: { "X-CSRF-TOKEN": csrfdata.token },
          credentials: "include",
          method: "post",
          body: formData,
        });
        let data = await request.json();
        console.log(data);

        let userdata = JSON.parse(localStorage.getItems("user"));
        userdata.transaction = data.message;
        localStorage.setItems("user", JSON.stringify(userdata));
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
